import React, { useRef, forwardRef, useImperativeHandle } from 'react';
import clsx from 'clsx';
import {
    InputLabelDefaultProps,
    InputLabelPropTypes
} from './generated/propTypes';

const InputLabel = forwardRef((props, externalRef) => {
    const {
        children,
        className,
        disabled,
        hideLabel,
        targetId,
        ...customProps
    } = props;
    const ref = useRef();

    useImperativeHandle(externalRef, () => ref.current, []);

    return (
        <label
            ref={ref}
            className={clsx(
                'lmnt-input-label',
                hideLabel && 'sr-only',
                disabled && 'lmnt-theme-on-surface-disabled',
                className
            )}
            htmlFor={targetId}
            aria-labelledby={targetId}
            {...customProps}
        >
            {children}
        </label>
    );
});

InputLabel.displayName = 'InputLabel';
InputLabel.propTypes = InputLabelPropTypes;
InputLabel.defaultProps = InputLabelDefaultProps;

export default InputLabel;
